@charset "UTF-8";
/* 회사소개 */
.company_feature {
	overflow: hidden;
}

.company_feature .item_feature {
	float: left;
	width: 290px;
	border: 1px solid #c8cfd9;
	box-sizing: border-box;
}

.company_feature .item_feature + .item_feature {
	margin-left: 30px;
}

.company_feature .main_feature {
	position: relative;
	height: 236px;
	padding: 18px 19px;
	box-sizing: border-box;
}

.company_feature .main_feature:before {
	background-image: url(../img/sp_company.png);
	position: absolute;
	background-repeat: no-repeat;
	content: ' ';
}

.company_feature .main_feature.feat_story:before {
	width: 194px;
	height: 131px;
	background-position: 0px -145px;
	bottom: 21px;
	left: 45px;
}

.company_feature .main_feature.feat_us:before {
	width: 218px;
	height: 135px;
	background-position: 0px 0px;
	bottom: 12px;
	left: 26px;
}

.company_feature .main_feature.feat_value:before {
	width: 220px;
	height: 123px;
	background-position: -228px 0px;
	bottom: 17px;
	left: 42px;
}

.company_feature .menu_feat {
	position: relative;
	display: block;
	padding: 13px 19px 14px;
	background-color: #fafafa;
	border-top: 1px solid #c8cfd9;
}

.company_feature .menu_feat:before {
	background-image: url(../img/sp_common.png);
	width: 9px;
	height: 17px;
	background-position: -48px -48px;
	position: absolute;
	top: 15px;
	right: 19px;
	content: '';
}

.company_feature .menu_feat:hover, .company_feature .menu_feat:focus {
	background-color: #344763;
	color: #fff;
}

.desc_feature {
	margin-top: 7px;
	font-size: 12px;
	color: #777;
	line-height: 1.45;
}

.company_info {
	margin-top: 30px;
}

.list_company_info {
	overflow: hidden;
}

.list_company_info .line_content_wrap {
	float: left;
	width: 290px;
}

.list_company_info .line_content_wrap.info_slider {
	position: relative;
	height: 305px;
	padding: 20px;
	border-bottom: 1px solid #c8cfd9;
}

.list_company_info .line_content_wrap + .line_content_wrap {
	margin-left: 30px;
}

.list_company_info .line_content_wrap .common_title {
	color: #333;
}

.list_company_info .line_content_wrap .item_wrap {
	height: 150px;
}

.list_company_info .line_content_wrap .item_wrap:before {
	background-image: url(../img/sp_company.png);
	position: absolute;
	background-repeat: no-repeat;
	content: '';
}

.list_company_info .line_content_wrap .item_wrap.info_bg_logo:before {
	width: 92px;
	height: 72px;
	background-position: -352px -145px;
	right: 20px;
	bottom: 43px;
}

.list_company_info .line_content_wrap .item_wrap.info_bg_ballon:before {
	width: 74px;
	height: 127px;
	background-position: 0px -286px;
	top: 8px;
	right: 13px;
}

.list_company_info .line_content_wrap .item_wrap.info_bg_magazin {
	border-bottom: 1px solid #c8cfd9;
}

.list_company_info .line_content_wrap .item_wrap.info_bg_magazin:before {
	width: 138px;
	height: 104px;
	background-position: -204px -145px;
	right: 5px;
	bottom: 8px;
}

.list_company_info .line_content_wrap .item_wrap.info_bg_mihan {
	border-bottom: 1px solid #c8cfd9;
}

.list_company_info .line_content_wrap .item_wrap.info_bg_mihan:before {
	width: 136px;
	height: 36px;
	background-position: -84px -286px;
	right: 18px;
	bottom: 32px;
}

.box_workspace {
	position: absolute;
	bottom: 0;
	left: 0;
	background-color: rgba(52, 71, 99, 0.8);
	padding: 5px 10px;
}

.box_workspace > strong {
	font-size: 13px;
	font-weight: 400;
	color: #fff;
}

.workspace_dot {
	text-align: center;
	margin-top: 7px;
}

.workspace_dot .swiper-pagination-bullet {
	background-color: #cecece;
	opacity: 1;
	margin: 0 4px;
}

.workspace_dot .swiper-pagination-bullet-active {
	background-color: #344763;
}

.slider_wrokspace {
	margin-top: 15px;
}

.btn_arrow {
	position: absolute;
	top: 50%;
}

.btn_arrow.arrow_prev {
	left: 0;
	transform: rotateY(180deg);
}

.btn_arrow.arrow_next {
	right: 8px;
}

.btn_arrow:before {
	background-image: url(../img/sp_common.png);
	width: 9px;
	height: 17px;
	background-position: -48px -48px;
	display: inline-block;
	content: '';
}

.employ_content {
	overflow: hidden;
}

.employ_content + .employ_content {
	margin-top: 30px;
}

.employ_content > .box_employ {
	position: relative;
	float: left;
	width: 450px;
	box-sizing: border-box;
}

.employ_content > .box_employ + .box_employ {
	margin-left: 30px;
}

.employ_content .line_box {
	border: 1px solid #c9d1d7;
	padding: 18px 20px 16px;
	height: 250px;
}

.employ_content [class*=bg_]:before {
	position: absolute;
	content: '';
}

.employ_content .line_content_wrap:last-child {
	border-bottom: 1px solid #c9d1d7;
}

.list_employ_tab {
	position: relative;
	margin-top: 18px;
}

.list_employ_tab:after {
	display: block;
	clear: both;
	content: '';
}

.list_employ_tab:before {
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	height: 1px;
	background-color: #1B375B;
	content: '';
}

.list_employ_tab > li {
	float: left;
}

.list_employ_tab > li + li {
	margin-left: 1px;
}

.list_employ_tab > li.on .title_tab {
	position: relative;
	background-color: #fff;
	border: 1px solid #1B375B;
	border-bottom: 1px solid #fff;
	color: #1B375B;
}

.list_employ_tab > li.on .box_tab_content {
	display: block;
	opacity: 1;
	visibility: visible;
}

.list_employ_tab .title_tab {
	display: block;
	min-width: 88px;
	padding: 8px 0;
	border: 1px solid transparent;
	background-color: #8396b2;
	font-weight: 500;
	color: #fff;
	outline: none;
	cursor: pointer;
}

.list_employ_tab .title_tab:hover, .list_employ_tab .title_tab:focus {
	background-color: #7286a3;
}

.box_tab_content {
	display: none;
	position: absolute;
	right: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
}

.box_tab_content .swiper-slide {
	text-align: center;
}

.box_tab_content .item_employ {
	display: inline-table;
	margin-top: 33px;
	padding: 0 20px;
	table-layout: fixed;
}

.box_tab_content .item_employ:hover, .box_tab_content .item_employ:focus {
	text-decoration: underline;
}

.box_tab_content .item_employ .title_employ {
	display: table-cell;
	vertical-align: middle;
	width: 66px;
	height: 66px;
	background-color: #344763;
	color: #fff;
	text-align: center;
	border-radius: 50%;
}

.box_tab_content .tab_info_employ {
	display: table-cell;
	vertical-align: middle;
	padding-left: 25px;
	text-align: left;
}

.box_tab_content .tab_info_employ > li {
	font-size: 13px;
}

.box_tab_content .tab_info_employ > li:before {
	display: inline-block;
	vertical-align: middle;
	width: 3px;
	height: 3px;
	border-radius: 50%;
	background-color: #888;
	content: '';
}

.box_tab_content .tab_info_employ > li.point {
	color: #21528c;
}

.box_tab_content .tab_info_employ > li.point:before {
	width: auto;
	height: auto;
	margin-right: 3px;
	background-color: transparent;
	border-radius: none;
	content: '*';
}

.box_tab_content .tab_info_employ > li + li {
	margin-top: 10px;
}

.box_tab_content .tab_info_employ .title {
	font-weight: 400;
}

.box_tab_content > button {
	position: absolute;
	top: 50%;
	z-index: 10;
	color: #aebbc4;
	font-size: 18px;
	outline: none;
}

.box_tab_content > button:hover, .box_tab_content > button:focus {
	color: #344763;
}

.box_tab_content > button.xi-angle-left {
	left: -5px;
}

.box_tab_content > button.xi-angle-right {
	right: -5px;
}

.box_tab_content .swiper-pagination {
	text-align: center;
	right: 0;
	left: 0;
	bottom: -25px;
}

.box_tab_content .swiper-pagination > span {
	background-color: #43657c;
}

.box_tab_content .swiper-pagination > span + span {
	margin-left: 10px;
}

.box_tab_content .no_content {
	min-height: 100px;
	padding: 10px 0;
	text-align: center;
}

.box_tab_content .no_content:before {
	display: inline-block;
	vertical-align: middle;
	height: 110px;
	width: 1px;
	content: '';
}

.box_tab_content .no_content > strong {
	font-weight: 400;
	color: #888;
}

.bg_telescope:before {
	background-image: url(../img/sp_employ.png);
	width: 297px;
	height: 141px;
	background-position: 0px 0px;
	right: 30px;
	bottom: 20px;
}

.bg_graph {
	height: 175px;
}

.bg_graph:before {
	background-image: url(../img/sp_employ.png);
	width: 189px;
	height: 128px;
	background-position: -307px 0px;
	top: 27px;
	right: 20px;
}

.bg_puzzle {
	height: 175px;
}

.bg_puzzle:before {
	background-image: url(../img/sp_employ.png);
	width: 192px;
	height: 152px;
	background-position: 0px -151px;
	top: 15px;
	right: 15px;
}

.item_skill {
	height: 350px;
}

.item_skill .list_work {
	overflow: hidden;
	padding-top: 15px;
	margin: -8px 0 0 -8px;
}

.item_skill .list_work > li {
	float: left;
	margin: 8px 0 0 8px;
}

.item_skill .list_work > li > a {
	position: relative;
	display: block;
	width: 200px;
	height: 114px;
	padding: 20px 15px;
	background-color: #f5f5f5;
	box-sizing: border-box;
}

.item_skill .list_work > li > a:hover, .item_skill .list_work > li > a:focus {
	background-color: #344763;
}

.item_skill .list_work > li > a:hover .name_skil, .item_skill .list_work > li > a:focus .name_skil {
	color: #fff;
}

.item_skill .list_work > li > a:before {
	background-image: url(../img/sp_employ.png);
	position: absolute;
	top: 0;
	right: 0;
	content: '';
}

.item_skill .list_work > li > a.item_building:before {
	width: 121px;
	height: 105px;
	background-position: 0px -313px;
}

.item_skill .list_work > li > a.item_car:before {
	width: 121px;
	height: 105px;
	background-position: -131px -313px;
	top: 8px;
}

.item_skill .list_work > li > a.item_box:before {
	width: 121px;
	height: 105px;
	background-position: -307px -138px;
}

.item_skill .list_work > li > a.item_sketch:before {
	width: 121px;
	height: 105px;
	background-position: -262px -313px;
	top: 0px;
	right: 10px;
}

.item_skill .list_work .name_skil {
	font-weight: 400;
	color: #777;
}

.split_content:after {
	display: block;
	clear: both;
	content: '';
}

.split_content .split_left {
	width: 410px;
}

.split_content .split_left .common_title + .list_title_notice {
	margin-top: 20px;
}

.split_content .split_right {
	width: 490px;
}

.split_content + .split_content {
	margin-top: 14px;
}

.list_title_notice > li {
	position: relative;
}

.list_title_notice > li + li {
	margin-top: 17px;
	padding-top: 17px;
}

.list_title_notice > li + li:before {
	position: absolute;
	top: 0;
	right: -100%;
	left: -100%;
	height: 1px;
	background-color: #c8cfd9;
	content: '';
}

.list_title_notice .title_notice {
	font-size: 16px;
	font-weight: 600;
	color: #333;
}

.list_title_notice .title_notice + .list_notie_item {
	margin-top: 15px;
}

.list_title_notice .baisc_more {
	position: absolute;
	top: 4px;
	right: -2px;
}

.list_notie_item > li {
	position: relative;
	line-height: 1.5;
}

.list_notie_item > li:before {
	position: absolute;
	top: 9px;
	left: 0;
	width: 3px;
	height: 3px;
	margin: 0 5px 0 0;
	border-radius: 50%;
	background-color: #95a6b2;
	vertical-align: middle;
	content: '';
}

.list_notie_item > li + li {
	margin-top: 5px;
}

.list_notie_item > li > a {
	display: block;
	overflow: hidden;
	padding-left: 10px;
}

.list_notie_item > li > a:hover .name_noticle, .list_notie_item > li > a:focus .name_noticle {
	text-decoration: underline;
	color: #333;
}

.list_notie_item .name_noticle {
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
	display: block;
	font-size: 13px;
	max-width: 80%;
	color: #767676;
}

.list_notie_item .data_notice {
	font-size: 13px;
	color: #767676;
}

.wrap_pr_item .item_pr_box {
