}

.wrap_pr_item .item_pr_box.type_line {
	width: 190px;
	height: 220px;
	padding: 15px 0;
	border: 1px solid #c8cfd9;
}

.wrap_pr_item .item_pr_box:hover, .wrap_pr_item .item_pr_box:focus {
