}

.wrap_pr_item .item_pr_box .sub_title {
	font-size: 12px;
	color: #668dc6;
}

.slider_event_area {
	position: relative;
}

.slider_event_area .btn_arw {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	z-index: 10;
	color: #fff;
	font-size: 26px;
}

.slider_event_area .btn_arw.xi-angle-left-thin {
	left: 10px;
}

.slider_event_area .btn_arw.xi-angle-right-thin {
	right: 10px;
}

.item_history {
	min-height: 158px;
	background-color: #f9f9f9;
	background-image: url(../img/pr_monitor.png);
	background-repeat: no-repeat;
	background-position: right 72px bottom 10px;
}

.video_frame {
	margin-top: 30px;
}

.video_frame iframe {
	width: 100%;
}

.box_ir_left {
	width: 610px;
}

.box_ir_right {
	width: 290px;
}

.box_company_info {
	padding: 24px 15px;
	border: 1px solid #c8cfd9;
}

.box_company_info .title {
	font-size: 24px;
	color: #344763;
}

.box_company_info .title + .desc_title {
	margin-top: 12px;
}

.box_company_info .desc_title {
	color: #5b8cd5;
}

.list_ir_feature {
	display: table;
	width: 100%;
	margin-top: 15px;
	table-layout: fixed;
}

.list_ir_feature > li {
	display: table-cell;
	vertical-align: middle;
}

.list_ir_feature > li > a:hover .title_item, .list_ir_feature > li > a:focus .title_item {
	text-decoration: underline;
}

.list_ir_feature .title_item {
	margin-top: 4px;
	font-size: 14px;
	font-weight: 600;
}

.stock_content .baisc_more {
	position: absolute;
	top: 24px;
	right: 20px;
}

.stock_wrap {
	padding: 20px 20px 15px;
}

.main_stock_wrap {
	margin-top: 20px;
}

.main_stock_wrap .name_stock {
	font-size: 13px;
	color: #777;
}

.main_stock_wrap .main_stock_state {
	text-align: right;
}

.main_stock_wrap .main_stock_state .value {
	display: block;
	font-weight: 600;
	font-size: 26px;
	letter-spacing: -1px;
}

.main_stock_wrap .main_stock_state .state {
	display: inline-block;
	position: relative;
	margin-top: 4px;
	font-size: 15px;
	font-weight: 600;
}

.main_stock_wrap .main_stock_state .state:before {
	display: inline-block;
	vertical-align: middle;
	margin: -2px 5px 0 0;
	content: '';
}

.main_stock_wrap .main_stock_state .state.up {
	color: #ff552b;
}

.main_stock_wrap .main_stock_state .state.up:before {
	border-style: solid;
	border-width: 0 7px 10px 7px;
	border-color: transparent transparent #ff552b transparent;
}

.list_ir_stock > li {
	padding: 10px 20px;
	border-top: 1px solid #c8cfd9;
}

.list_ir_stock .name_stock {
	font-size: 13px;
	color: #777;
	font-weight: normal;
}

.list_ir_stock .date_stock {
	font-size: 12px;
	color: #777;
}

.list_ir_stock .state_stock {
	display: inline-block;
	min-width: 50px;
	margin-left: 5px;
	font-size: 12px;
	color: #666;
	text-align: right;
}

.list_ir_stock .state_stock.up {
	color: #ff562c;
}

.list_ir_stock .state_stock.down {
	color: #4384e4;
}

