}

.item_ir_data {
	background: #f9f9f9 url(../img/ir_book.png) no-repeat;
	background-position: right 12px bottom 12px;
}

.list_button_content > li {
	overflow: hidden;
	padding: 7px 22px;
	border-bottom: 1px solid #c9d1d7;
}

.list_button_content > li:first-child {
	border-top: 1px solid #c9d1d7;
}

.list_button_content > li .text {
	float: left;
	margin-top: 6px;
	font-size: 13px;
	color: #777;
}

.list_button_content > li .btn_wrap {
