}

.list_button_content > li .btn_ir {
	display: inline-block;
	padding: 5px 15px;
	border: 1px solid #abb9cd;
	color: #4b6388;
}

.list_button_content > li .btn_ir:hover, .list_button_content > li .btn_ir:focus {
	color: #fff;
	background-color: #4b6388;
	border-color: #4b6388;
}
